.ticket-section {
    padding: 20px;
  }
  
  .section-title {
    font-size: 24px;
    margin-bottom: 20px;
  }
  
  .ticket-container {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }
  
  .ticket-box {
    border: 2px solid #ddd;
    border-radius: 10px;
    padding: 20px;
    width: 300px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    background-color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  
  .ticket-name {
    font-size: 20px;
    margin-bottom: 10px;
  }
  
  .ticket-description {
    font-size: 16px;
    margin-bottom: 15px;
  }
  
  .ticket-details {
    font-size: 14px;
    margin-bottom: 20px;
  }
  
  .registration-button {
    padding: 10px 20px;
    font-size: 16px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    align-self: flex-start;
  }
  
  .registration-button.free {
    background-color: #28a745; /* Green color */
    color: white;
  }
  
  .registration-button.paid {
    background-color: #007bff; /* Blue color */
    color: white;
  }
  