@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@900&display=swap');

.event-info-section {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-left: 20px; 
   
    font-weight: bold; /* Add this line to make the text bold */
  }
  
  
  .event-details {
    margin-bottom: 20px;
    margin-left: 20px;

  }
  
  .button-container {
    display: flex;
    margin-bottom: 20px;
    margin-top: 30px;
  }
  
  .official-website-button,
  .buy-now-button {
    padding: 15px 40px;
    font-size: 18px;
    font-weight: bold;
    color: #fff;
    background-color: #ff7f00;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s;
    text-decoration: none;
    margin-left: 10px;
   
  }
  
  .official-website-button:hover,
  .buy-now-button:hover {
    background-color: #cc6600;
  }
  
 
.timer {
    align-self: flex-end;
    margin-top: 1px; /* Adjusted margin-top */
    display: flex;
  }
  
  .timer-box {
    margin-right: 20px;
    padding: 10px;
    font-size: 18px;
    font-weight: bold;
    color: #ff7f00; /* Orange color */
    border: 2px solid #ff7f00; /* Orange border */
    border-radius: 5px;
  }
  
.event-name {
    font-family: 'Roboto', sans-serif; /* Use the custom font */
    font-size: 36px;
    font-weight: 900; /* Use the extra-bold variant */
    margin-bottom: 10px;
  }
  .event-poster {
    max-width: 500px; 
    margin-left:900px;
    margin-top: -250px;/* Adjust the maximum width of the image */
  }