/* Header.css */

header {
    display: flex;
    justify-content: flex-end; /* Move content to the right */
    align-items: center;
    padding: 20px;
    background-color: #333;
    color: white;
  }
  
  nav ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  nav ul li {
    display: inline;
    margin-right: 20px;
  }
  
  nav ul li:last-child {
    margin-right: 0;
  }
  
  nav ul li a {
    text-decoration: none;
    color: white;
  }
  